function useScrollLock(isActive: boolean) {
    React.useEffect(() => {
        if (!isActive) {
            return;
        }

        const origHeight = window.document.documentElement.style.height;
        const origOverflow = window.document.documentElement.style.overflow;

        window.document.documentElement.style.height = "100%;";
        window.document.documentElement.style.overflow = "hidden";

        return () => {
            window.document.documentElement.style.height = origHeight;
            window.document.documentElement.style.overflow = origOverflow;
        };
    }, [isActive]);
}

export function useFocusTrap(enable: boolean) {
    const { instanceId, validateTabbable, inputs } = useValuSearch();

    useEffect(() => {
        if (!enable) {
            return;
        }

        const selectors = [
            `#valu-search-container-${instanceId}`,
            `.valu-search-screen-reader-${instanceId}`,
            `.valu-search-focus-trap-container-${instanceId}`,
            `.valu-search-results-viewer-${instanceId}`,
        ].join(",");

        const htmlElements = Array.from(
            window.document.querySelectorAll(selectors),
        ).filter((el): el is HTMLElement => {
            return el instanceof HTMLElement;
        });

        const containers: HTMLElement[] = [...inputs, ...htmlElements];

        if (containers.length === 0) {
            throw Error(
                `Valu Search - No containers found with focus trap selectors. Selectors: ${selectors}`,
            );
        }

        const trap = new FocusTrap({
            containers: containers,
            validateTabbable: validateTabbable,
        });

        trap.enable();

        return () => {
            trap.disable();

            // Just focus the first input if there was no element active when
            // the trap was enabled. This happens for example when search
            // results were opened directly via link
            if (
                // XXX WTF the screen header gets focused on load? Maybe because
                // tabindex=0. Is it good idea?
                trap.elementBeforeTrap?.classList.contains(
                    "valu-search-screen-reader-vs",
                )
            ) {
                inputs[0]?.focus();
            }
        };
    }, [instanceId, enable, validateTabbable, inputs]);
}

interface ScrollContextType {
    listeners: OnScrollType[];
}
type OnScrollType = (e: React.UIEvent<HTMLDivElement>) => any;

const ScrollContext = React.createContext<ScrollContextType>({
    listeners: [],
});

export function useParentScroll(onScroll: OnScrollType) {
    const context = React.useContext(ScrollContext);
    if (!context) {
        throw new Error("Cannot find ScrollContext");
    }
    React.useEffect(() => {
        context.listeners.push(onScroll);

        return () => {
            const listenerIndex = context.listeners.indexOf(onScroll);
            if (listenerIndex !== -1) {
                context.listeners.splice(listenerIndex, 1);
            }
        };
    });
}

export function Main(props: { topMargin: number | string | undefined }) {
    const isSearchActive = useIsSearchActive();
    const isSearching = useIsSearching();
    const dispatch = useDispatch();
    const {
        uiStrings,
        instanceId,
        setAnimationFinished,
        animationFinished,
        deactivate,
    } = useValuSearch();

    const contextRef = React.useRef<ScrollContextType>({ listeners: [] });

    const onScroll: OnScrollType = (e) => {
        for (const listener of contextRef.current.listeners) {
            listener(e);
        }
    };

    useScrollLock(isSearchActive);

    useEffect(() => {
        if (!isSearchActive) {
            setAnimationFinished(false);
            emit({ name: "closed" });
            dispatch(SearchActions.resetResults());
        } else {
            emit({ name: "opened" });
            setTimeout(() => {
                setAnimationFinished(true);
            }, 350);
            // should return clear on dismount
        }
    }, [isSearchActive, dispatch, setAnimationFinished]);

    let style = {};
    if (props.topMargin !== undefined) {
        style = {
            top: u(props.topMargin),
        };
    }

    return (
        <ScrollContext.Provider value={contextRef.current}>
            <Blk
                id={"valu-search-container-" + instanceId}
                data-testid={"valu-search-container" as any}
                className={"valu-search-modal valu-search-modal-" + instanceId}
                open={isSearchActive}
                style={style}
            >
                <Blk.Container
                    role="search"
                    open={isSearchActive}
                    animationFinished={animationFinished}
                    onScroll={onScroll}
                >
                    <Blk.ScrollBarEqualizer>
                        <Blk.UIWrapper>
                            <Slot name="mainWrap" params={{}}>
                                <Slot name="modalHeader" params={{}}>
                                    <FullScreenInputWithCloseButton />
                                </Slot>
                                <Blk.SpinnerContainer
                                    visible={isSearching}
                                    aria-hidden={
                                        isSearching ? undefined : "true"
                                    }
                                >
                                    <Spinner />
                                </Blk.SpinnerContainer>
                                <SearchResultsViewer />
                                <Blk.CloseIconContainer
                                    animationFinished={animationFinished}
                                >
                                    <Slot
                                        name="closeButton"
                                        params={{ close: deactivate }}
                                    >
                                        <Blk.CloseIconContainer.Button
                                            aria-label={uiStrings.closeSearch}
                                            data-testid="close-button"
                                            onClick={deactivate}
                                        >
                                            <Blk.CloseIconContainer.Text>
                                                {uiStrings.close}
                                            </Blk.CloseIconContainer.Text>
                                            <Blk.CloseIconContainer.Icon>
                                                <CloseIcon size={32} />
                                            </Blk.CloseIconContainer.Icon>
                                        </Blk.CloseIconContainer.Button>
                                    </Slot>
                                </Blk.CloseIconContainer>
                                <Slot name="valuSearchAd" params={{}}>
                                    <ValuSearchAd></ValuSearchAd>
                                </Slot>
                            </Slot>
                        </Blk.UIWrapper>
                    </Blk.ScrollBarEqualizer>
                </Blk.Container>
            </Blk>
        </ScrollContext.Provider>
    );
}
