export interface SearchResult {
    tagGroupId: string;
    created: Date;
    modified: Date;
    score: number;
    title: string;
    url: string;
    highlight: string;
}

export interface ResultsWithTotal {
    tagGroupId: string;
    id: string;
    hits: SearchResult[];
    total: number;
}

export interface State {
    status: "initial" | "fetching" | "ready";
    error: {
        source: "fetch" | "other";
        message: string;
    } | null;

    results: {
        [groupId: string]:
            | {
                  hits: SearchResult[];
                  total: number;
              }
            | undefined;
    };
}

export const initialState: State = {
    status: "initial",
    error: null,
    results: {},
};
