import { debounce } from "./utils/helpers";

export type ValuSearchEvent =
    | {
          name: "opened";
      }
    | {
          name: "closed";
      }
    | {
          name: "search-result-clicked";
          terms: string;
          url: string;
          resultTitle: string;
      }
    | {
          name: "search";
          terms: string;
      }
    | {
          name: "search-debounced";
          terms: string;
      }
    | {
          name: "group-results-clicked";
          terms: string;
          groupTitle: string;
      }
    | {
          name: "group-nav-clicked";
          terms: string;
          groupTitle: string;
      }
    | {
          name: "back-button-clicked";
          terms: string;
          groupTitle: string;
      }
    | {
          name: "search-response";
          terms: string;
          countTotal: number;
          responseGroups: {
              groupTitle: string;
              total: number;
              lang: string | undefined;
          }[];
          lang: string;
      }
    | {
          name: "search-response-debounced";
          terms: string;
          countTotal: number;
          responseGroups: {
              groupTitle: string;
              total: number;
              lang: string | undefined;
          }[];
          lang: string;
      }
    | {
          name: "search-response-group-view";
          terms: string;
          countTotal: number;
          responseGroups: {
              groupTitle: string;
              total: number;
              lang: string | undefined;
          }[];
          lang: string;
      }
    | {
          name: "search-result-clicked-extended";
          terms: string;
          url: string;
          resultTitle: string;
          normalizedRelevancy: number;
          position: number;
          lang: string;
          uiLocation: string;
          groupTitle: string;
          bestResult: { title: string; url: string };
      };

export interface ValuSearchEventListener {
    (event: ValuSearchEvent): void;
}

const LISTENERS = [] as ValuSearchEventListener[];

export function addValuSearchEventListener(fn: ValuSearchEventListener) {
    LISTENERS.push(fn);
}

export function emit(event: ValuSearchEvent) {
    LISTENERS.forEach((fn) => fn(event));

    let browserEvent;

    try {
        browserEvent = new Event("valu-search-event", {
            bubbles: true,
            cancelable: true,
        });
    } catch (error) {
        // catch IE error
        browserEvent = window.document.createEvent("Event");
        browserEvent.initEvent("valu-search-event", true, true);
    }
    (browserEvent as any).valuSearchEvent = event;
    window.document.dispatchEvent(browserEvent);
}

export function emitSearchResponseEvent(options: {
    terms: string;
    responses: {
        groupTitle: string;
        total: number;
        lang: string | undefined;
    }[];
    lang: string | undefined;
}) {
    const countTotal = options.responses.reduce(
        (sum, current) => sum + current.total,
        0,
    );

    emit({
        name: "search-response",
        terms: options.terms,
        countTotal: countTotal,
        responseGroups: options.responses,
        lang: options.lang ?? "unknown",
    });

    emitSearchResponseDebounced({ ...options, countTotal });
}

const emitSearchResponseDebounced = debounce(
    (options) =>
        emit({
            name: "search-response-debounced",
            terms: options.terms,
            countTotal: options.countTotal,
            responseGroups: options.responses,
            lang: options.lang ?? "unknown",
        }),
    500,
);

export function emitSearchResultClickedEvent(options: {
    resultTitle: string;
    url: string;
    terms: string;
    score: number;
    lang: string | undefined;
    qs: URLSearchParams;
    groupTitle: string;
    instanceId: string;
}) {
    const resultsShown = window.document.querySelectorAll(".SearchResultTitle");

    const bestResult = resultsShown[0];

    if (!(bestResult instanceof HTMLElement)) {
        return;
    }

    if (!bestResult.dataset.score) {
        return;
    }

    if (!(bestResult.firstChild instanceof HTMLAnchorElement)) {
        return;
    }

    const bestScore = parseFloat(bestResult.dataset.score);

    if (!bestScore) {
        return;
    }

    const titles = Array.from(resultsShown).map((item) => {
        // .SearchResultTitle has only one child <a>
        if (item.firstChild instanceof HTMLElement) {
            return item.firstChild.innerHTML;
        } else {
            throw Error("Bad child for SearchResultTitle encountered");
        }
    });

    const index = titles.indexOf(options.resultTitle);
    const normalizedRelevancy = options.score / bestScore;

    const isMain = options.qs.has(options.instanceId + "_id");
    let uiLocation;
    if (isMain) {
        uiLocation = "MainView";
    } else {
        uiLocation = "GroupDetailView";
    }

    emit({
        name: "search-result-clicked-extended",
        terms: options.terms,
        resultTitle: options.resultTitle,
        url: options.url,
        normalizedRelevancy: normalizedRelevancy,
        position: index + 1,
        lang: options.lang ?? "unknown",
        uiLocation: uiLocation,
        groupTitle: options.groupTitle,
        bestResult: {
            title: bestResult.firstChild.innerHTML,
            url: bestResult.firstChild.href,
        },
    });
}

export function emistSearchEvent(options: { terms: string }) {
    emit({ name: "search", terms: options.terms });
    emistSearchEventDebounced(options);
}

const emistSearchEventDebounced = debounce(
    (options) => emit({ name: "search-debounced", terms: options.terms }),
    500,
);

export function emitSearchResponseGroupViewEvent(options: {
    terms: string;
    responses: {
        groupTitle: string;
        total: number;
        lang: string | undefined;
    }[];
    lang: string | undefined;
}) {
    const countTotal = options.responses.reduce(
        (sum, current) => sum + current.total,
        0,
    );

    emit({
        name: "search-response-group-view",
        terms: options.terms,
        countTotal: countTotal,
        responseGroups: options.responses,
        lang: options.lang ?? "unknown",
    });
}
