/**
 * Retuns a title component with provided values with event emitter
 * @param props
 */
export function SearchResultTitle(props: {
    title: string;
    url: string;
    score: number;
}) {
    return (
        <HitTitle
            data-testid={"hit-title"}
            data-score={props.score}
            className="SearchResultTitle"
        >
            <SearchResultUrl
                title={props.title}
                url={props.url}
                score={props.score}
                renderedTitle={props.title}
            />
        </HitTitle>
    );
}

/**
 * Returns an excerpt text component in <p> tag
 * @param props
 */
export function SearchResultExcerpt(props: {
    highlight: string;
    showInUi?: Boolean;
}) {
    const { uiStrings } = useValuSearch();
    if (props.showInUi === false) {
        return <></>;
    }

    return (
        <SearchResultExcerptBlk
            data-testid={"hit-excerpt"}
            aria-description={uiStrings.searchResultExcerpt}
            dangerouslySetInnerHTML={{
                __html: props.highlight,
            }}
        ></SearchResultExcerptBlk>
    );
}

/**
 * Returns Date component in <span> converted to a local time string according to config.lang
 * Use this for created and modified dates
 * @param props
 */
export function SearchResultDate(props: { date: Date; showInUi?: Boolean }) {
    const { uiStrings, lang } = useValuSearch();

    if (props.showInUi === false) {
        return <></>;
    }

    return (
        <SearchResultDateBlk
            aria-label={
                uiStrings.searchResultDate +
                " " +
                new Date(props.date).toLocaleDateString(lang)
            }
            data-testid="hit-date"
        >
            {new Date(props.date).toLocaleDateString(lang)}
        </SearchResultDateBlk>
    );
}

