import { useGroupSearchResults, useIsSearching } from "../redux/hooks";
import { useSearchTerms } from "../utils/use-search-terms";
import ResultList from "./ResultList";
import { css } from "react-bemed/css";
import { u, useGetInnerLinkFromWindow } from "../utils/helpers";
import { useValuSearch, useCurrentGroup } from "../config";
import { Slot } from "./Slot";
import { emit } from "../events";
import { bemed } from "./bemed";
import { GroupLinkContainer } from "./GrouppedResultPreview";
import { useParentScroll } from "./Main";
import { ValuSearchLink } from "./SharedComponents";
import { ArrowRightIcon, ArrowLeftIcon } from "./ReactIcons";
import React from "react";

const Blk = bemed({
    name: "Container",

    css: css`
        display: block;
        width: 100%;
    `,

    mods: {
        opaque: css`
            opacity: 0.55;
        `,
    },

    elements: {
        Header: bemed({
            css: css`
                position: sticky;
                position: -webkit-sticky;
                top: ${u(85)};
                background-color: white;
                z-index: 100;
            `,
        }),
        Scrollable: bemed({
            css: css`
                z-index: 99;
            `,
        }),
        SpinnerContainer: bemed({
            css: css`
                margin-left: auto;
                margin-right: auto;
                padding: ${u(15)} 0 ${u(25)} 0;
            `,
        }),
    },
});

const GroupResultsButton = bemed({
    name: "GroupResultsButton",
    as: "button",
    className: "reset-button",

    css: css`
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 100;
        width: 100%;
        max-width: ${u(750)};
        padding: 0;
        border: none;
        outline: none;
        font: inherit;
        color: inherit;
        background: none;
    `,

    elements: {
        ButtonTitle: bemed({
            as: "h3",
            css: css`
                display: flex;
                flex-direction: row;
                align-items: center;
                margin-bottom: 0;
            `,
        }),
        ArrowIcon: bemed({
            css: css`
                display: flex;
                flex-direction: row;
                margin: auto ${u(0)} auto ${u(15)};
            `,
        }),
    },
});

function MoreLink(props: { more: string; onSearchMore: () => void }) {
    const { uiStrings } = useValuSearch();

    switch (props.more) {
        case "more":
            return (
                <GroupResultsButton onClick={props.onSearchMore}>
                    <GroupResultsButton.ButtonTitle>
                        {uiStrings.moreResults}
                        <GroupResultsButton.ArrowIcon>
                            <ArrowRightIcon size={24} />
                        </GroupResultsButton.ArrowIcon>
                    </GroupResultsButton.ButtonTitle>
                </GroupResultsButton>
            );
        case "nomore":
            return (
                <GroupLinkContainer.LinkTitle data-testid="all-results-shown-message">
                    {uiStrings.allShown}
                </GroupLinkContainer.LinkTitle>
            );
        default:
            return null;
    }
}

export function GroupResults() {
    const {
        uiStrings,
        searchMore,
        pairGroupResultsWithTagGroupId,
    } = useValuSearch();
    const group = useCurrentGroup();
    const currentResults = useGroupSearchResults(group.id);
    const getInnerLinkFromWindow = useGetInnerLinkFromWindow();
    const terms = useSearchTerms();
    const isSearching = useIsSearching();

    const currentResultsWithTagGroupId = {
        total: currentResults.total,
        hits: pairGroupResultsWithTagGroupId({
            searchResults: currentResults.hits,
            groupId: group.id,
        }),
    };

    const fetchedAllResults =
        currentResults.hits.length >= currentResults.total;

    const more = fetchedAllResults ? "nomore" : "more";

    useParentScroll((e) => {
        const el = e.target as HTMLDivElement;
        const isAtBottom =
            el.clientHeight !== el.scrollHeight &&
            el.scrollTop + el.clientHeight >= el.scrollHeight;
        if (isAtBottom && !fetchedAllResults && !isSearching) {
            searchMore();
        }
    });

    return (
        <Blk
            opaque={useIsSearching()}
            data-testid="group-result-scroll-container"
        >
            <Slot name="groupResultsHeaderWrap" params={{}}>
                <Blk.Header>
                    <GroupLinkContainer>
                        <ValuSearchLink
                            href={getInnerLinkFromWindow()}
                            dataTestId="back-to-preview-link"
                            onClick={() =>
                                emit({
                                    name: "back-button-clicked",
                                    terms: terms,
                                    groupTitle: group.title,
                                })
                            }
                        >
                            <GroupLinkContainer.LinkTitle>
                                <GroupLinkContainer.ArrowIcon back>
                                    <ArrowLeftIcon size={24} />
                                </GroupLinkContainer.ArrowIcon>
                                {uiStrings.back}
                            </GroupLinkContainer.LinkTitle>
                        </ValuSearchLink>
                    </GroupLinkContainer>
                </Blk.Header>
            </Slot>
            <Slot name="groupResultsWrap" params={{}}>
                <Blk.Scrollable>
                    <ResultList
                        title={group.title}
                        results={currentResultsWithTagGroupId}
                    />

                    {!isSearching && (
                        <GroupLinkContainer>
                            <MoreLink
                                more={more}
                                onSearchMore={() => searchMore()}
                            />
                        </GroupLinkContainer>
                    )}
                </Blk.Scrollable>
            </Slot>
        </Blk>
    );
}
