import { State, SearchResult } from "./state";
import {
    debug,
    featureFlag,
    isDeepEqual,
    requestIdleCallback,
} from "../utils/helpers";
import { SearchActions } from "./actions";
import produce from "immer";
import { MultiSearchResponse } from "..";
import { batch } from "react-redux";

export interface ValuSearchStore {
    getState(): State;
    dispatch(action: { type: string }): void;
    subscribe(cb: Function): () => void;
}

class IdleDispatchStore implements ValuSearchStore {
    private state: State;
    private pendingActions = [] as any[];
    private store: ValuSearchStore;

    constructor(store: ValuSearchStore) {
        console.warn("[RVS]  Using experimental IdleDispatchStore!");
        this.store = store;
        this.state = store.getState();
    }

    getState() {
        return this.state;
    }

    dispatch(action: any) {
        this.pendingActions.push(action);

        if (this.pendingActions.length > 1) {
            debug("Skipping useless state update", this.pendingActions);
            return;
        }

        const started = performance.now();

        requestIdleCallback(() => {
            const duration = performance.now() - started;
            debug(
                `Deferred ${this.pendingActions.length} Redux dispatches by ${duration}`,
            );

            batch(() => {
                for (const a of this.pendingActions) {
                    this.store.dispatch(a);
                }
                this.pendingActions = [];
            });
        });
    }

    subscribe(cb: () => void) {
        return this.store.subscribe(cb);
    }
}

export interface StaticSearchParams {
    groupIds: string[];
    searchEndpoint: string;
    apiKey: string; // XXX is there  a reason for this to be optional ??
}

export interface DynamicSearchParams {
    groupConfig: {
        [groupId: string]: Omit<SearchGroupParams, "id"> | undefined;
    };
    groupState: {
        [groupId: string]: SearchGroupState | undefined;
    };
    lang: string | undefined;
    terms: string;
}

function mergeDynamicParams(
    existing: DynamicSearchParams,
    update: Partial<DynamicSearchParams>,
): DynamicSearchParams {
    return produce(existing, (draft) => {
        const draftConfig: DynamicSearchParams["groupConfig"] =
            draft.groupConfig;

        for (const key in update.groupConfig) {
            Object.assign(draftConfig[key], update.groupConfig[key]);
        }

        Object.assign(draft, update, { groupConfig: draftConfig });
    });
}

export interface SearchGroupState {
    from?: number;
}

export interface SearchGroupParams {
    id: string;
    tagGroupId?: string;
    tagQuery: string[][];
    createdDecay?: number;
    modifiedDecay?: number;
    decayScale?: string;
    highlightLength?: number;
    size?: number;
    from?: number;
    lang?: string;
}

export interface FullSearchParams {
    terms: string;
    groups: SearchGroupParams[];
    searchEnpoint: string;
    apiKey?: string;
}

/**
 * Fethcer is almost like ResultsWithTotal but it does not have the
 * "tagGroupId" property which is added on the client-side
 */
export interface FetcherResponse {
    hits: Omit<SearchResult, "tagGroupId">[];
    total: number;
}

export interface ValuSearchResponseFetcher {
    (params: FullSearchParams): Promise<MultiSearchResponse[]>;
}

export function buildAPIRequestURL(params: FullSearchParams): string {
    // TODO Refactor backend to take size and from on per group basis ??
    const size = Math.max(
        ...params.groups.map((group) => {
            return group.size ?? 5;
        }),
    );
    const from = Math.max(
        ...params.groups.map((group) => {
            return group.from ?? 0;
        }),
    );

    const reqJSON = {
        q: params.terms,
        size: size,
        from: from,
        groups: params.groups.map((group) => {
            return {
                tagQuery: group.tagQuery,
                createdDecay: group.createdDecay,
                modifiedDecay: group.modifiedDecay,
                decayScale: group.decayScale,
                highlightLength: group.highlightLength,
                lang: group.lang,
            };
        }),
    };

    return (
        params.searchEnpoint +
        "?data=" +
        encodeURIComponent(JSON.stringify(reqJSON))
    );
}

export const searchRequest: ValuSearchResponseFetcher = () => {
    return Promise.resolve([]);
};

function assertInputEvent(e: {
    target?: any;
}): asserts e is { target: HTMLInputElement } {
    if (!(e.target instanceof HTMLInputElement)) {
        throw new Error("Not HTMLInputElement");
    }
}

export interface AddressBarListener {
    listen(cb: (event: { triggerSearch: boolean }) => any): () => void;
    setTerms(terms: string): void;
    setGroupId(id: string): void;
    getTerms(): string;
    getGroupId(): string; //  XXX | undefined;
}

export interface SearchEngineOptions {
    store: ValuSearchStore;
    addressBarListener: AddressBarListener;
    fetcher: ValuSearchResponseFetcher;
    staticSearchParams: StaticSearchParams;
    initialDynamicSearchParams: DynamicSearchParams;
    throttleTime?: number;
    searchMoreSize?: number;
    minSearchTermsLength?: number;
    onSearch: (dynamicSearchParams: DynamicSearchParams) => void;
    onSearchResponse: (options: {
        fullSearchParams: FullSearchParams;
        responses: MultiSearchResponse[];
    }) => void;
}

export class SearchEngine {
    store: ValuSearchStore;
    impl: ValuSearchResponseFetcher;
    dynamicSearchParams: DynamicSearchParams;
    staticSearchParams: StaticSearchParams;
    throttleTime: number;
    throttleTimerID?: ReturnType<typeof setTimeout>;
    addressBarListener: AddressBarListener;
    searchMoreSize: number;
    minSearchTermsLength: number;
    onSearch: (dynamicSearchParams: DynamicSearchParams) => void;
    onSearchResponse: (options: {
        fullSearchParams: FullSearchParams;
        responses: MultiSearchResponse[];
    }) => void;
    nextFetchParams?: DynamicSearchParams;
    nextAppendGroupId: string | undefined;
    lastAppendGroupId: string | undefined;
    requestId = 0;
    pendingRequestIds: Set<number> = new Set();
    currentResultsId?: number;
    destroyed?: boolean;
    unbindAddressBarListeners: () => void;

    inputs = [] as {
        input: HTMLInputElement;
        onChange: (e: { target: unknown }) => void;
        onEnter: (e: KeyboardEvent) => void;
    }[];

    constructor(options: SearchEngineOptions) {
        if (featureFlag("experimentalIdleThrottle")) {
            this.store = new IdleDispatchStore(options.store);
        } else {
            this.store = options.store;
        }

        this.impl = options.fetcher;
        this.addressBarListener = options.addressBarListener;
        this.throttleTime = options.throttleTime ?? 500;
        this.dynamicSearchParams = options.initialDynamicSearchParams;
        this.staticSearchParams = options.staticSearchParams;
        this.searchMoreSize = options.searchMoreSize ?? 20;
        this.minSearchTermsLength = options.minSearchTermsLength ?? 2;
        this.onSearch = options.onSearch;
        this.onSearchResponse = options.onSearchResponse;

        this.unbindAddressBarListeners = this.addressBarListener.listen(
            (event) => {
                if (!event.triggerSearch) {
                    return;
                }

                const terms = this.addressBarListener.getTerms();

                this.api.setSearchParams({
                    terms: terms,
                });

                this.syncInputs(terms ?? "", { force: true });
            },
        );
    }

    destroy = () => {
        this.getInputs().forEach((input) => {
            this.removeInput(input);
        });
        this.unbindAddressBarListeners();
        this.destroyed = true;
    };

    initialSearchCalled = false;

    doInitialSearch = () => {
        if (this.initialSearchCalled) {
            return;
        }

        this.initialSearchCalled = true;

        // trigger search if there was search params on url
        if (this.addressBarListener.getTerms().trim() !== "") {
            this.dynamicSearchParams = mergeDynamicParams(
                this.dynamicSearchParams,
                { terms: this.addressBarListener.getTerms() },
            );

            this.nextAppendGroupId = this.addressBarListener.getGroupId();

            // dynamic params and next params might be the same
            // during initial search, it's important to trigger
            // the search anyway
            this.nextFetchParams = this.dynamicSearchParams;
            this.api.skipThrottle();
        }
    };

    subscribe = (cb: Function): (() => void) => {
        return this.store.subscribe(cb);
    };

    getStatus = () => {
        return this._status;
    };

    getState = () => {
        return this.store.getState();
    };

    getGroupResults = (group: string): SearchResult[] => {
        return this.store.getState().results[group]?.hits ?? [];
    };

    /**
     * Only a setStatus() cache
     */
    private _status?: State["status"];

    setStatus = (status: State["status"]) => {
        // Avoid updating the store if the state does not change
        if (this._status !== status) {
            this._status = status;
            this.store.dispatch(SearchActions.setStatus(status));
        }
    };

    setErrored = (errored: State["error"]) => {
        this.store.dispatch(SearchActions.setError(errored));
    };

    syncInputs = (terms: string, options?: { force: boolean }) => {
        for (const input of this.inputs) {
            if (options?.force && input) {
                input.input.value = terms;
                continue;
            }
            // only change input value if it does not have focus
            if (input && input.input !== document.activeElement) {
                input.input.value = terms;
            }
        }
    };

    createOnIdleChange(input: HTMLInputElement) {
        let pendingIdle = false;

        return () => {
            if (pendingIdle) {
                debug("Defering input change using request idle callback");
                return;
            }

            const started = performance.now();
            pendingIdle = true;
            requestIdleCallback(() => {
                pendingIdle = false;

                debug(
                    "Deferred setSearchParams() by " +
                        (performance.now() - started),
                );

                this.api.setSearchParams({
                    terms: input.value,
                });
            });
        };
    }

    /**
     * Bind input to search. Returns true when is new input is added. False if
     * the given input was already added
     */
    addInput = (input: HTMLInputElement) => {
        const prev = this.inputs.find((o) => o.input === input);
        if (prev) {
            return false;
        }

        const urlbarTerms = this.addressBarListener.getTerms().trim();

        if (urlbarTerms) {
            // Enable search results linking by copying the terms to the input
            // from url bar but skip if if the input is active so we wont mess
            // with the user too much
            if (input.value.trim() === "" || input !== document.activeElement) {
                input.value = this.addressBarListener.getTerms() ?? "";
            }
        } else if (input.value.trim()) {
            // Other way around. If user manages to write something to the input
            // before this is called, use that value to make a search. This is
            // mainly for lazy loading when the input can be interacted with
            // before this .addInput() call
            this.api.setSearchParams({ terms: input.value });
        }

        let onChange = (e: { target: unknown }): any => {
            assertInputEvent(e);
            this.api.setSearchParams({
                terms: e.target.value,
            });
        };

        if (featureFlag("experimentalIdleThrottle")) {
            onChange = this.createOnIdleChange(input);
        }

        const onEnter = (e: KeyboardEvent) => {
            if (e.key === "Enter") {
                this.api.skipThrottle();
            }
        };

        input.addEventListener("input", onChange);
        input.addEventListener("keydown", onEnter);

        this.inputs.push({ input, onChange, onEnter });
        return true;
    };

    getInputs() {
        return this.inputs.map((o) => o.input);
    }

    removeInput = (rmInput: HTMLInputElement) => {
        const input = this.inputs.find((input) => input?.input === rmInput);

        input?.input.removeEventListener("keydown", input.onEnter);

        input?.input.removeEventListener("change", input.onChange);

        const inputIndex = this.inputs.findIndex((obj) => obj === input);
        this.inputs.splice(inputIndex, 1);
    };

    getGroupById = (
        groupId: string,
    ): Omit<SearchGroupParams, "id"> | undefined => {
        const group = this.dynamicSearchParams.groupConfig[groupId];

        return group;
    };

    getFullParams = (options: {
        shouldReset?: boolean;
        appendGroupId: string | undefined;
    }) => {
        const fullParams: FullSearchParams = {
            terms: this.dynamicSearchParams.terms,
            searchEnpoint: this.staticSearchParams.searchEndpoint,
            apiKey: this.staticSearchParams.apiKey,
            groups: this.staticSearchParams.groupIds
                .filter((id) => {
                    if (!options.appendGroupId) {
                        return true;
                    }

                    return id === options.appendGroupId;
                })
                .map((id) => {
                    const group = this.dynamicSearchParams.groupConfig[id];
                    if (!group) {
                        throw new Error(`Cannot find group for id ${id}`);
                    }

                    const getSize = () => {
                        if (options.appendGroupId) {
                            return this.searchMoreSize;
                        } else {
                            return group.size ?? 5;
                        }
                    };

                    const getFrom = () => {
                        if (options.appendGroupId && !options.shouldReset) {
                            return (
                                this.getState().results[options.appendGroupId]
                                    ?.hits.length ?? 0
                            );
                        } else {
                            return group.from ?? 0;
                        }
                    };

                    return {
                        id,
                        ...group,
                        from: getFrom(),
                        size: getSize(),
                        lang: this.dynamicSearchParams.lang,
                    };
                }),
        };
        return fullParams;
    };

    fetch = (shouldReset?: boolean) => {
        if (!this.nextFetchParams) {
            return;
        }
        if (this.destroyed) {
            return;
        }
        this.dynamicSearchParams = this.nextFetchParams;
        const appendGroupId = this.nextAppendGroupId;
        const id = this.requestId;

        const fullParams = this.getFullParams({
            shouldReset: shouldReset,
            appendGroupId: appendGroupId,
        });

        this.pendingRequestIds.add(id);
        if (
            this.addressBarListener.getTerms().trim() !==
            this.dynamicSearchParams.terms.trim()
        ) {
            this.addressBarListener.setTerms(this.dynamicSearchParams.terms);
        }
        //we need to sync input values from url bar and if there are multiple search inputs
        this.syncInputs(this.dynamicSearchParams.terms);

        if (
            this.dynamicSearchParams.terms.trim().length <
            this.minSearchTermsLength
        ) {
            this.fakeEmptyRequestResponse({ id: id, fullParams });
            return;
        }

        this.onSearch(this.dynamicSearchParams);

        this.lastAppendGroupId = appendGroupId;

        this.impl(fullParams).then(
            (res) => {
                if (this.destroyed) {
                    return;
                }
                // there are newer search results already displayed
                if (this.currentResultsId && id < this.currentResultsId) {
                    return;
                }

                // Combine responses with the search groups and re-assign the ids for them
                const resWithIds: State["results"] = {};

                fullParams.groups.forEach((group, index) => {
                    resWithIds[group.id] = {
                        hits: res[index].hits.map((hit) => {
                            return {
                                ...hit,
                                tagGroupId: group.tagGroupId ?? group.id,
                            };
                        }),
                        total: res[index].total,
                    };
                });

                if (appendGroupId && !shouldReset) {
                    this.store.dispatch(
                        SearchActions.addAllResults(resWithIds),
                    );
                } else {
                    this.store.dispatch(
                        SearchActions.setAllResults(resWithIds),
                    );
                }

                this.currentResultsId = id;

                // remove id and all smaller ids from pending requests
                this.pendingRequestIds.forEach((pendingRequestId) => {
                    if (pendingRequestId <= id) {
                        this.pendingRequestIds.delete(pendingRequestId);
                    }
                });

                this.setReady();

                this.onSearchResponse({
                    fullSearchParams: fullParams,
                    responses: res,
                });
            },
            (error) => {
                // remove id from pending requests
                this.pendingRequestIds.delete(id);

                this.setReady();

                this.setErrored({
                    source: "fetch",
                    message:
                        error?.message ||
                        String(error) ||
                        "unknown fetch error",
                });

                console.error("Valu Search fetch error", error, error?.message);
                return;
            },
        );
    };

    triggerFetch = () => {
        if (this.throttleTimerID) {
            return;
        }

        if (!this.nextFetchParams) {
            this.setReady();
            return;
        }

        const isEqual = isDeepEqual(
            this.dynamicSearchParams,
            this.nextFetchParams,
        );

        // this is ignored in group details OR if the last search was in group details
        if (isEqual && !this.nextAppendGroupId && !this.lastAppendGroupId) {
            // stop sending requests in groupped preview if
            // user changed search terms during throttle and
            // they are the same as last search params

            // Clear the nextFetchParams since they were equal with previous
            // one, so there's nothing to do with them. Also mark as ready if
            // there are no other requests in flight.
            this.nextFetchParams = undefined;
            this.setReady();
            return;
        }

        this.realFetch(!isEqual);
        this.throttleTimerID = setTimeout(() => {
            this.throttleTimerID = undefined;
            this.triggerFetch();
        }, this.throttleTime);
    };

    realFetch = (shouldReset: boolean) => {
        if (this.nextFetchParams) {
            this.setStatus("fetching");
            this.requestId += 1;

            this.fetch(shouldReset);

            this.nextFetchParams = undefined;
        }
    };

    fakeEmptyRequestResponse = (options: {
        id: number;
        fullParams: FullSearchParams;
    }) => {
        // there are newer search results already displayed
        if (this.currentResultsId && options.id < this.currentResultsId) {
            return;
        }

        // Combine responses with the search groups and re-assign the ids for them
        const resWithIds: State["results"] = {};

        options.fullParams.groups.forEach((group) => {
            resWithIds[group.id] = {
                hits: [],
                total: 0,
            };
        });

        this.store.dispatch(SearchActions.setAllResults(resWithIds));

        this.currentResultsId = options.id;

        this.pendingRequestIds.clear();

        this.setReady();
    };

    termsEmpty = () => {
        return this.dynamicSearchParams.terms === "";
    };

    setReady = () => {
        // if there are any in flight requests OR pending requests do not set ready
        if (this.pendingRequestIds.size === 0 && !this.nextFetchParams) {
            this.setStatus("ready");
        }
    };

    api = {
        skipThrottle: () => {
            if (!this.nextFetchParams) {
                return;
            }

            if (this.throttleTimerID) {
                clearTimeout(this.throttleTimerID);
                this.throttleTimerID = undefined;
            }
            this.realFetch(true);
        },

        // XXX this is useless now, no reason why group id should not be read on setSearchParams
        searchMore: () => {
            this.api.setSearchParams({});
        },

        setSearchParams: (params: Partial<DynamicSearchParams>) => {
            const appendGroupId = this.addressBarListener.getGroupId();
            const nextParams = mergeDynamicParams(
                this.dynamicSearchParams,
                params,
            );

            this.nextFetchParams = nextParams;
            this.nextAppendGroupId = appendGroupId;

            if (this.throttleTimerID) {
                return;
            }
            this.triggerFetch();
        },
    };
}
