import React from "react";
import { css } from "react-bemed/css";
import {
    useSearchResults,
    useGroupSearchResults,
    useIsSearching,
} from "../redux/hooks";
import { useSearchTerms } from "../utils/use-search-terms";
import ResultList from "./ResultList";
import { u, useGetInnerLinkFromWindow } from "../utils/helpers";
import { useValuSearch, useCurrentGroup } from "../config";
import { emit } from "../events";
import { GroupIdProvider } from "./GroupInfo";
import { bemed } from "./bemed";
import { ValuSearchLink } from "./SharedComponents";
import { Slot } from "./Slot";
import { ArrowRightIcon } from "./ReactIcons";

const Blk = bemed({
    name: "GrouppedResultPreview",

    css: css`
        width: 100%;
        backface-visibility: hidden;
        transform: translateZ(0);
        -webkit-font-smoothing: subpixel-antialiased;
    `,

    mods: {
        opaque: css`
            opacity: 0.55;
        `,
    },
});

const GroupContainer = bemed({
    name: "SearchGroupContainer",

    css: css`
        padding-bottom: ${u(11)};
    `,

    mods: {
        opaque: css`
            opacity: 0.55;
        `,
    },
});

export const GroupLinkContainer = bemed({
    name: "GroupLinkContainer",

    css: css`
        display: flex;
        flex-direction: row;
        align-items: center;
        height: ${u(36)};
        z-index: 100;
        background-color: white;
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        max-width: ${u(750)};
    `,

    elements: {
        Link: bemed({
            as: "a",
            css: css`
                text-decoration: none;
                :hover {
                    text-decoration: underline;
                }
            `,
        }),
        LinkTitle: bemed({
            as: "h3",
            css: css`
                flex-direction: row;
                align-items: center;
                display: flex;
            `,
        }),
        ArrowIcon: bemed({
            css: css`
                display: flex;
                flex-direction: row;
                margin: auto ${u(0)} auto ${u(15)};
            `,
            mods: {
                forward: css``,
                back: css`
                    margin-left: ${u(-7)};
                `,
            },
        }),
    },
});

const emptyResult = {
    hits: [],
    total: 0,
};

function MoreLink(props: { q: string }) {
    const group = useCurrentGroup();
    const currentResults = useGroupSearchResults(group.id);
    const getInnerLinkFromWindow = useGetInnerLinkFromWindow();
    const { uiStrings } = useValuSearch();
    const terms = useSearchTerms();

    let more: string;
    if (currentResults.total === 0) {
        more = "none";
    } else if (currentResults.total > 5) {
        more = "more";
    } else {
        more = "nomore";
    }

    switch (more) {
        case "more":
            return (
                <ValuSearchLink
                    href={getInnerLinkFromWindow({
                        searchTerms: props.q,
                        groupIndex: group.tagGroupId,
                    })}
                    dataTestId="all-group-results-link"
                    onClick={() =>
                        emit({
                            name: "group-results-clicked",
                            terms: terms,
                            groupTitle: group.title,
                        })
                    }
                >
                    <GroupLinkContainer.LinkTitle
                        aria-label={
                            uiStrings.moreResults +
                            " " +
                            uiStrings.moreResultsIn +
                            " " +
                            group.title
                        }
                    >
                        <Slot
                            name="groupLinkTitleWrap"
                            params={{
                                groupTitle: group.title,
                                ariaLabel:
                                    uiStrings.moreResults +
                                    " " +
                                    uiStrings.moreResultsIn +
                                    " " +
                                    group.title,
                                type: "more",
                            }}
                        >
                            {uiStrings.moreResults}
                        </Slot>
                        <GroupLinkContainer.ArrowIcon forward>
                            <ArrowRightIcon size={24} />
                        </GroupLinkContainer.ArrowIcon>
                    </GroupLinkContainer.LinkTitle>
                </ValuSearchLink>
            );
        case "nomore":
            return (
                <GroupLinkContainer.LinkTitle
                    aria-label={
                        uiStrings.allShown +
                        " " +
                        uiStrings.moreResultsIn +
                        " " +
                        group.title
                    }
                >
                    <Slot
                        name="groupLinkTitleWrap"
                        params={{
                            groupTitle: group.title,
                            ariaLabel:
                                uiStrings.allShown +
                                " " +
                                uiStrings.moreResultsIn +
                                " " +
                                group.title,
                            type: "allShown",
                        }}
                    >
                        {uiStrings.allShown}
                    </Slot>
                </GroupLinkContainer.LinkTitle>
            );
        case "none":
            return (
                <GroupLinkContainer.LinkTitle
                    aria-label={
                        uiStrings.noResults +
                        " " +
                        uiStrings.moreResultsIn +
                        " " +
                        group.title
                    }
                >
                    <Slot
                        name="groupLinkTitleWrap"
                        params={{
                            groupTitle: group.title,
                            ariaLabel:
                                uiStrings.noResults +
                                " " +
                                uiStrings.moreResultsIn +
                                " " +
                                group.title,
                            type: "noResults",
                        }}
                    >
                        {uiStrings.noResults}
                    </Slot>
                </GroupLinkContainer.LinkTitle>
            );
        default:
            return null;
    }
}

export function GrouppedResultPreview() {
    const results = useSearchResults();
    const {
        tagGroups,
        orderBy,
        previewGroupSize,
        pairGroupResultsWithTagGroupId,
    } = useValuSearch();
    const isPending = useIsSearching();

    const terms = useSearchTerms();

    const sortArray = tagGroups.map((group, index) => {
        const groupResults = results[index] || emptyResult;
        return {
            id: String(index),
            title: group.title,
            groupResults: {
                total: groupResults.total,
                hits: pairGroupResultsWithTagGroupId({
                    searchResults: groupResults.hits,
                    groupId: String(index),
                }),
            },
            scoreBoost: tagGroups[index].scoreBoost ?? 1,
        };
    });

    switch (orderBy) {
        case "tagName":
            sortArray.sort((a, b) => {
                return a.title.localeCompare(b.title);
            });
            break;
        case "total":
            sortArray.sort((a, b) => {
                return b.groupResults.total - a.groupResults.total;
            });
            break;
        case "score":
            sortArray.sort((a, b) => {
                const aVal = a.groupResults.hits[0]
                    ? a.groupResults.hits[0].score * a.scoreBoost
                    : 0;
                const bVal = b.groupResults.hits[0]
                    ? b.groupResults.hits[0].score * b.scoreBoost
                    : 0;
                return bVal - aVal;
            });
            break;
        case "originalNotEmpties":
            sortArray.sort((a, b) => {
                if (a.groupResults.total === 0) {
                    return 1;
                }
                if (b.groupResults.total === 0) {
                    return -1;
                }
                return 0;
            });
    }

    return (
        <Blk opaque={isPending}>
            {sortArray.map((group) => {
                return (
                    <GroupIdProvider key={group.title} id={group.id}>
                        <GroupContainer
                            opaque={group.groupResults.hits.length === 0}
                        >
                            <ResultList
                                title={group.title}
                                results={group.groupResults}
                                limitResultCount={previewGroupSize ?? 5}
                            />
                            <GroupLinkContainer>
                                <MoreLink q={terms} />
                            </GroupLinkContainer>
                        </GroupContainer>
                    </GroupIdProvider>
                );
            })}
        </Blk>
    );
}
