import React, { useState, useEffect } from "react";
import { css } from "react-bemed/css";
import { useSearchResults, useIsSearching } from "../redux/hooks";
import { useValuSearch, useGroupDetailsId } from "../config";
import { Slot } from "./Slot";

import { bemed } from "./bemed";
import { GroupResults } from "./GroupResults";
import { GrouppedResultPreview } from "./GrouppedResultPreview";
import { useSearchTerms } from "../utils/use-search-terms";
import { usePreviousValue, u } from "../utils/helpers";

const Blk = bemed({
    name: "ResultsResolver",

    css: css`
        width: 100%;
        backface-visibility: hidden;
        transform: translateZ(0);
        -webkit-font-smoothing: subpixel-antialiased;
    `,

    mods: {
        opaque: css`
            opacity: 0.55;
        `,
    },
});

// off screen element per https://webaim.org/techniques/css/invisiblecontent/#techniques
// instruction for screen reader content
const SRContainerBlk = bemed({
    name: "SRContainer",
    as: "div",

    css: css`
        position: absolute;
        left: ${u(-10000)};
        top: auto;
        width: 1px;
        height: 1px;
        overflow: hidden;
    `,

    elements: {
        AsH1: bemed({
            as: "h1",
            css: css`
                position: absolute;
                left: ${u(-10000)};
                top: auto;
                width: 1px;
                height: 1px;
                overflow: hidden;
            `,
        }),
    },
});

function SearchResultsMainTitle() {
    const { uiStrings, instanceId } = useValuSearch();
    return (
        <SRContainerBlk.AsH1
            id="vs-searchResultHeader"
            className={`valu-search-main-title-${instanceId}`}
        >
            {uiStrings.searchResultsTitle}
        </SRContainerBlk.AsH1>
    );
}

function SearchInstructions() {
    const { uiStrings, instanceId } = useValuSearch();
    return (
        <SRContainerBlk id={`valu-search-instructions-${instanceId}`}>
            {uiStrings.searchInstructions}
        </SRContainerBlk>
    );
}

function ScreenReaderContainer() {
    const searchResults = useSearchResults();
    const searchTerms = useSearchTerms();
    const prevSearchTerm = usePreviousValue(searchTerms);
    const { uiStrings, tagGroups, instanceId } = useValuSearch();
    const groupId = useGroupDetailsId();

    const SR_DEBOUNCE_TIME = 400;

    const [srMessage, setSRMessage] = useState("");

    let searchResultTotal: number = 0;
    for (const index in searchResults) {
        const groupTotal = searchResults[index]?.total;
        if (groupTotal) {
            searchResultTotal += groupTotal;
        }
    }

    // debounced sr message hook
    useEffect(() => {
        //do not read this when navigating between groups
        if (searchTerms === prevSearchTerm) {
            return;
        }
        const timer = setTimeout(
            () =>
                setSRMessage(
                    `${uiStrings.searchTerm}: ${searchTerms}. ${uiStrings.searchResults}: ${searchResultTotal}`,
                ),
            SR_DEBOUNCE_TIME,
        );

        return () => clearTimeout(timer);
    }, [
        searchResults,
        searchTerms,
        searchResultTotal,
        uiStrings,
        prevSearchTerm,
    ]);

    // immediate sr message hook
    useEffect(() => {
        if (groupId === null) {
            const timer = setTimeout(
                () =>
                    setSRMessage(
                        `${uiStrings.searchTerm}: ${searchTerms}. ${uiStrings.searchResults}: ${searchResultTotal}`,
                    ),
                SR_DEBOUNCE_TIME,
            );

            return () => clearTimeout(timer);
        }

        const groupIndex = Number(groupId);

        setSRMessage(
            `${uiStrings.searchResultGroup}: ${tagGroups[groupIndex].title}. ${uiStrings.searchResults}: ${searchResults[groupId]?.total}`,
        );
    }, [
        groupId,
        searchResultTotal,
        uiStrings,
        searchResults,
        searchTerms,
        tagGroups,
    ]);

    return (
        <SRContainerBlk
            aria-live="polite"
            data-testid="sr-live-messages"
            id={`valu-search-screen-reader-live-messages-${instanceId}`}
        >
            {srMessage}
        </SRContainerBlk>
    );
}

const emptyResult = {
    hits: [],
    total: 0,
};

export function SearchResultsViewer() {
    const results = useSearchResults();
    const { tagGroups, orderBy, instanceId } = useValuSearch();
    const isSearching = useIsSearching();
    const groupId = useGroupDetailsId();
    const isDetails = groupId !== null;

    const sortArray = tagGroups.map((group, index) => {
        const groupResults = results[String(index)] || emptyResult;
        return {
            id: String(index),
            title: group.title,
            groupResults: groupResults,
            scoreBoost: tagGroups[index].scoreBoost ?? 1,
        };
    });

    switch (orderBy) {
        case "tagName":
            sortArray.sort((a, b) => {
                return a.title.localeCompare(b.title);
            });
            break;
        case "total":
            sortArray.sort((a, b) => {
                return b.groupResults.total - a.groupResults.total;
            });
            break;
        case "score":
            sortArray.sort((a, b) => {
                const aVal = a.groupResults.hits[0]
                    ? a.groupResults.hits[0].score * a.scoreBoost
                    : 0;
                const bVal = b.groupResults.hits[0]
                    ? b.groupResults.hits[0].score * b.scoreBoost
                    : 0;
                return bVal - aVal;
            });
            break;
        case "originalNotEmpties":
            sortArray.sort((a, b) => {
                if (a.groupResults.total === 0) {
                    return 1;
                }
                if (b.groupResults.total === 0) {
                    return -1;
                }
                return 0;
            });
    }

    return (
        <Blk
            opaque={isSearching}
            className={`valu-search-results-viewer-${instanceId}`}
        >
            <SearchResultsMainTitle />
            <SearchInstructions />
            <ScreenReaderContainer />
            <Slot
                name="groupNav"
                params={{
                    groups: sortArray.map((group) => {
                        return {
                            title: group.title,
                            id: group.id,
                            total: group.groupResults.total,
                        };
                    }),
                }}
            ></Slot>
            <Slot name="resultsWrap" params={{}}>
                {isDetails ? <GroupResults /> : <GrouppedResultPreview />}
            </Slot>
        </Blk>
    );
}
