import React from "react";
import { css } from "react-bemed/css";
import { SearchResult } from "../redux/state";
import { u } from "../utils/helpers";
import { Slot } from "./Slot";
import { Hit } from "./SharedComponents";
import { bemed } from "./bemed";
import { useValuSearch } from "../config";

const JSXContainer = bemed({
    name: "GroupResults",

    css: css`
        margin: 0 auto;
        z-index: 100;
        width: 100%;
        max-width: ${u(750)};
    `,
});

const HitsContainer = bemed({
    name: "HitsContainer",

    css: css`
        display: block;
        padding-bottom: ${u(24)};
        z-index: 100;
    `,

    elements: {
        Button: bemed({
            as: "button",
            className: "reset-button",

            defaultProps: {
                type: "button",
            },
            css: css`
                display: flex;
                flex-direction: row;
                justify-content: center;
                border: none;
                height: ${u(65)};
                background: white;
                margin-left: auto;
                margin-right: auto;
                width: 100%;
                @media (min-width: ${u(768)}) {
                    width: ${u(511)};
                }
            `,
        }),
    },
});

const HitsInfo = bemed({
    name: "HitsInfo",

    css: css`
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: ${u(1)} solid #ccd5df;
    `,

    elements: {
        ResultTitle: bemed({
            as: "h2",
            css: css``,
        }),
        ResultsTotal: bemed({
            as: "h2",
            css: css`
                /* css */
            `,
        }),
    },
});

const emptyResult = {
    hits: [],
    total: 0,
};

export default function ResultList(props: {
    title: string;
    limitResultCount?: number;
    results: {
        hits: SearchResult[];
        total: number;
    };
}) {
    let hits = (props.results || emptyResult).hits;
    const { uiStrings, instanceId } = useValuSearch();

    if (props.limitResultCount) {
        hits = hits.slice(0, props.limitResultCount);
    }

    return (
        <JSXContainer>
            <HitsInfo>
                <Slot
                    name="groupTitle"
                    params={{
                        title: props.title,
                        ariaLabel:
                            uiStrings.searchResultGroup + " " + props.title,
                    }}
                >
                    <HitsInfo.ResultTitle
                        data-testid="results-group-title"
                        className={`valu-search-screen-reader-${instanceId}`}
                        aria-label={
                            uiStrings.searchResultGroup + " " + props.title
                        }
                    >
                        {props.title}
                    </HitsInfo.ResultTitle>
                </Slot>
                <Slot
                    name="groupTotal"
                    params={{
                        total: (props.results || emptyResult).total.toString(),
                        ariaLabel:
                            uiStrings.searchResults +
                            (props.results || emptyResult).total.toString(),
                    }}
                >
                    <HitsInfo.ResultsTotal
                        data-testid="results-group-total"
                        className={`valu-search-screen-reader-${instanceId}`}
                        aria-label={
                            uiStrings.searchResults +
                            " " +
                            (props.results || emptyResult).total
                        }
                    >
                        {(props.results || emptyResult).total}
                    </HitsInfo.ResultsTotal>
                </Slot>
            </HitsInfo>

            <HitsContainer>
                {hits.map((hit) => {
                    return (
                        <Slot
                            name="hit"
                            key={hit.url}
                            params={{
                                tagGroupId: hit.tagGroupId,
                                title: hit.title,
                                highlight: hit.highlight,
                                url: hit.url,
                                created: hit.created,
                                score: hit.score,
                                modified: hit.modified,
                            }}
                        >
                            <Hit
                                date={hit.created}
                                title={hit.title}
                                url={hit.url}
                                score={hit.score}
                                highlight={hit.highlight}
                            />
                        </Slot>
                    );
                })}
            </HitsContainer>
        </JSXContainer>
    );
}
